/* --------------常用距離設定開始--------------- */
$w-xxxs:0.3rem;
$w-xxs:0.5rem;
$w-xs:0.75rem;
$w-sm:1rem;
$w-md:1.5rem;
$w-lg:2rem;
$w-xl:3rem;
$w-xxl:5rem;
/* --------------顏色設定開始--------------- */
$primary-color: #ED831B;
$primary-light-color:#ffe8d2;
$danger-color:#D92F2F;
$special-color:#4976E1;
$stable-color:#409A4C;
$black:#000;
$gray900:#292929;
$gray800:#525252;
$gray700:#7a7a7a;
$gray600:#a3a3a3;
$gray500:#ccc;
$gray400:#d6d6d6;
$gray300:#e0e0e0;
$gray200:#ebebeb;
$gray100:#f5f5f5;
$white:#fff;


$primary-xxxlight-color:#A7DCFF;
$primary-xxlight-color:#83C6F2;
$primary-xlight-color:#58B4EF;
$primary-dark-color:#1379BB;
$primary-xdark-color:#135988;
$secondary-color:#F9BF00;
$secondary-light-color:#FFD15B;
$danger-dark-color:#E64343;
$alert-color:#F26C30;
$alert-light-color:#FF9130;
$alert-xlight-color:#FFA42D;
$tertiary-color:#3E659F;
$bright-color:#7DBF71;
$assist-color:#51BDC5;
$assist-dark-color:#61A7BE;
$base-color:#8d8d8d;
$base-light-color:#9FB1C2;
$stable-xlight-color:#d6ad98;
$pale-color:#F6F6F6;
$pale-light-color:#f8f8f7;

$color-themes: (
  //屬性: key, value
  'primary-color': $primary-color,
  'primary-light-color': $primary-light-color,
  'primary-dark-color': $primary-dark-color,
  'secondary-color':$secondary-color,
  'danger-color': $danger-color,
  'danger-dark-color': $danger-dark-color,
  'alert-color': $alert-color,
  'alert-light-color': $alert-light-color,
  'alert-xlight-color': $alert-xlight-color,
  'black': $black,
  'gray900': $gray900,
  'gray800': $gray800,
  'gray700': $gray700,
  'gray600': $gray600,
  'gray500': $gray500,
  'gray400': $gray400,
  'gray300': $gray300,
  'gray200': $gray200,
  'gray100': $gray100,
  'white': $white,
  'base-color': $base-color,
  'base-light-color': $base-light-color,
  'tertiary-color': $tertiary-color,
  'assist-color': $assist-color,
  'assist-dark-color': $assist-dark-color,
  'bright-color': $bright-color,
  'pale-color': $pale-color,
  'pale-light-color': $pale-light-color,
  'special-color': $special-color,
  'stable-color': $stable-color,
);
/* --------------字型設定開始--------------- */
$font-xxxs:.7rem;
$font-xxs:.8rem;
$font-xs:.9rem;
$font-xsm:.95rem;
$font-sm:1rem;
$font-smd:1.125rem;
$font-md:1.3rem;
$font-lg:1.5rem;
$font-xl:2rem;
$font-xxl:2.5rem;
$font-xxxl:3rem;
$font-xxxxl:5rem;

$font-size-themes: (
  //屬性: key, value
  'font-xxxs': $font-xxxs,
  'font-xxs': $font-xxs,
  'font-xs': $font-xs,
  'font-xsm': $font-xsm,
  'font-sm': $font-sm,
  'font-smd': $font-smd,
  'font-md': $font-md,
  'font-lg': $font-lg,
  'font-xl': $font-xl,
  'font-xxl': $font-xxxl,
  'font-xxxl': $font-xxxxl,
);

$font-xlight:100;
$font-light:400;
$font-bold:500;
$font-heavy:600;
$font-xheavy:800;

$font-weight-themes: (
  //屬性: key, value
  'font-xlight': $font-xlight,
  'font-light': $font-light,
  'font-bold': $font-bold,
  'font-heavy': $font-heavy,
  'font-xheavy': $font-xheavy,
);


@mixin gradient-horizon($primary-color,$gray) {
background: $gray; /* Old browsers */
background: -moz-linear-gradient(left,  $primary-color 0%, $gray 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(left,  $primary-color 0%,$gray 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to right,  $primary-color 0%,$gray 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

}

/* --------------按鈕設定開始--------------- */

@mixin btn-dark-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: darken($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-light-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: lighten($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-border($color){
  color: $color;
  border: 1px solid $color;
  padding: 5px 15px;
  &:hover,&.active{
    color: $white;
    background: $color;
    border: 1px solid $color;
  }
}
@mixin btn-border-mix($color,$bdcolor){
  color: $color;
  border: 1px solid $bdcolor;
  padding: 5px 15px;
  background: $white;
  &:hover,&.active{
    color: $white;
    background: $bdcolor;
    border: 1px solid $bdcolor;
  }
}
@mixin btn-blank($color){
  color: $color;
  background: $white;
  padding: 5px 15px;
  text-align: center;
  &:hover{
    color: $white;
    background: $color;
  }
}
/*連結設定開始*/
@mixin txt-link-dark($color){
  color: $color;
  &:hover{
    color: darken($color: $color, $amount: 10%);
  }
}
@mixin txt-link-light($color){
  color: $color;
  &:hover{
    color: lighten($color: $color, $amount: 15%);
  }
}
/*標題設定開始*/
@mixin top-title($color) {
  font-size: $font-xl;
  letter-spacing: 8px;
  font-weight: 600;
  color:$color;
  position: relative;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 30px;
  @include Pad{
    font-size:$font-lg;
    margin-bottom: 22px;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $color;
    left: 0;
    bottom: 0;
  }
}
/*表格設定開始*/
@mixin table-simple($bgcolor,$bdcolor){
  font-size: $font-sm;
  border: 2px solid $bdcolor;
  th{
      background: $bgcolor;
      border-bottom: 2px solid $bdcolor!important;
      color: $white;
      font-weight: $font-light;
      @include Pad{
        padding: .5rem;
      }
  }
  td{
    border-top: 1px solid $bdcolor;
    vertical-align: middle;
    @include Pad{
      padding: .5rem;
    }
  }
  td,th{
    border-color: $bdcolor;
  }
}