  line-height: 1.5;
  letter-spacing: 0.5px;
  color: #292929; }

.clear {
  clear: both;
  height: 0;
  font-size: 1px;
  line-height: 0px; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a,
