/*-----------------距離樣式---------------------*/
.mb-xs {
    margin-bottom: $w-xs;
}
/*-----------------連結樣式---------------------*/
@each $key, $value in $color-themes {
    .link-#{$key} {
        color: $value;
        &:hover {
            color: darken($value, 20%);
            img {
                filter: brightness(0.8);
            }
        }
    }
}
.link-img {
    &:hover {
        opacity: 0.8;
    }
}
.link-shadow {
    &:hover {
        box-shadow: 0 0 10px $gray500 !important;
    }
}
.nav-tabs {
    border-bottom: 2px solid $primary-light-color;
    .nav-item {
        margin-right: 10px;
    }
    .nav-link {
        color: $primary-color;
        border-radius: 10px 10px 0 0;
        border: 0;
        margin-bottom: -2px;
        &:hover {
            color: $primary-color;
            background: $gray100;
        }
        &.active {
            background: $primary-light-color;
            color: #fff;
            border: 1px solid $primary-light-color;
        }
    }
}
/*-----------------文字樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .#{$key} {
        color: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key} {
        font-size: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key}-lg {
        @include PC {
            font-size: $value !important;
        }
    }
    .#{$key}-md {
        @include Pad {
            font-size: $value !important;
        }
    }
}
@each $key, $value in $font-weight-themes {
    //文字重量
    .#{$key} {
        font-weight: $value !important;
    }
}
.txt-shadow {
    text-shadow: 0 0 5px $gray500;
}
.ls-0 {
    letter-spacing: normal;
}
/*-----------------背景樣式---------------------*/
@each $key, $value in $color-themes {
    .bg-#{$key} {
        background: $value !important;
    }
}
/*-----------------按鈕樣式---------------------*/
@each $key, $value in $color-themes {
    .btn-dark-#{$key} {
        @include btn-dark($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
@each $key, $value in $color-themes {
    .btn-border-#{$key} {
        @include btn-border($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.btn-dropdown {
    border-radius: 80px;
    background: $white;
    display: inline-flex;
    align-items: center;
    color: $primary-color !important;
    background: url("../images/ic-down-arrow.svg") no-repeat right center $white !important;
    color: $primary-color;
    padding-right: $w-lg;
    border: 1px solid $white !important;
    font-weight: $font-bold;
}

.toggle-menu {
    display: flex;
    border-bottom: 5px solid $danger-color;
    @include PC {
        border-bottom: 3px solid $danger-color;
    }
    &-item {
        display: block;
        border-radius: 6px 6px 0 0 !important;
        background: $gray500 !important;
        border: none !important;
        color: $white;
        letter-spacing: 1px;
        font-size: $font-smd;
        padding: 8px 24px !important;
        cursor: pointer;
        @include PC {
            font-size: $font-sm;
            padding: 6px 16px !important;
        }
        @include Pad {
            font-size: $font-xs;
        }
        @include mobile-lg {
            padding: 6px 12px !important;
        }
        &:hover {
            color: $white;
        }
        &.active {
            color: $white !important;
            background: $danger-color !important;
            border: none !important;
            pointer-events: none;
        }
    }
}
.icon-toggle-menu {
    border-bottom: 0;
    position: relative;
    &::after {
        content: " ";
        display: block;
        width: 1px;
        height: 100%;
        position: absolute;
        left: 50%;
        background: $primary-color;
    }
    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $font-lg;
        font-weight: $font-bold;
        color: $base-color;
        @include Pad {
            font-size: $font-md;
        }
        @include mobile-lg {
            font-size: $font-sm;
        }
        &:hover {
            color: $gray900;
        }
        &.active {
            color: $primary-color;
            pointer-events: none;
            &::after {
                background: url("../images/ic-arrow-active.svg");
                background-size: contain;
            }
        }
        &::after {
            content: " ";
            display: block;
            width: 18px;
            height: 18px;
            background: url("../images/ic-arrow.svg");
            background-size: contain;
            margin-left: 5px;
        }
        img {
            @include PC {
                width: 100px;
                margin-right: 5px;
            }
            @include mobile-lg {
                width: 60px;
            }
            @include mobile-md {
                display: none;
            }
        }
    }
}
.circle-btn {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.mobile-menu {
    &-item {
        color: $primary-color;
        font-weight: $font-bold;
        padding: 4px 24px;
        border-right: 1px solid $primary-color;
        &:last-child {
            border: none;
        }
    }
}
.icon-btn {
    display: inline-block;
    width: 24px;
    vertical-align: inherit;
    margin: 0 5px;
    cursor: pointer;
    &:hover {
        opacity: 0.8;
    }
}
.icon-txt-btn {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-box {
    @include Pad {
        font-size: $font-xs !important;
    }
    &-item {
        min-height: 45px;
    }
}
.btn-list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-bottom: 15px;
    height: 100%;
    &-header {
        display: flex;
        width: 100%;
        justify-content: center;
        padding-top: 5px;
        color: $primary-color;
        height: 40px;
        border-bottom: 1px solid lighten($color: $primary-color, $amount: 30%);
    }
    &-item {
        color: $gray900;
        text-decoration: underline;
        display: block;
        padding: 4px 0 4px 8px;
        &:hover {
            text-decoration: none;
            color: $primary-color;
        }
    }
}
.line-inline-btn {
    display: inline-flex;
    background: #4caf50;
    padding: 0 $w-xs;
    color: $white;
    border-radius: $w-xxxs;
    font-size: $font-sm;
    vertical-align: middle;
    @include Pad {
        font-size: $font-xs;
        img {
            width: 15px;
        }
    }
    &:hover {
        background: darken($color: #4caf50, $amount: 10%);
        color: $white;
    }
}
/* Sweep To Right */
.hvr-sweep-to-right {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}
.hvr-sweep-to-right:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $alert-light-color;
    border-radius: $w-xxs;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}
.hvr-sweep-to-right:hover,
.hvr-sweep-to-right:focus,
.hvr-sweep-to-right:active {
    color: white;
}
.hvr-sweep-to-right:hover:before,
.hvr-sweep-to-right:focus:before,
.hvr-sweep-to-right:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

/*-----------------邊框樣式---------------------*/
@each $key, $value in $color-themes {
    .bd-#{$key} {
        border-color: $value !important;
    }
}
/*-----------------麵包屑樣式-------------------*/
.breadcrumb {
    color: $white;
    margin-bottom: 0;
    padding: 2px 0 5px 0;
    &-link {
        color: $white !important;
        font-size: $font-xs;
        &:hover {
            opacity: 0.8;
        }
    }
    &-item {
        font-size: $font-xs;
        &.active {
            color: $white;
        }
        & + .breadcrumb-item::before {
            content: "";
            display: inline-block;
            width: $w-md;
            height: 100%;
            background: url("../images/ic-route.svg") no-repeat left center;
        }
    }
}
/*-----------------標題樣式---------------------*/
.title {
    color: $primary-color;
    position: relative;
    padding-left: 20px;
    display: block;
    &:before {
        content: "";
        display: block;
        position: absolute;
        width: 8px;
        height: 8px;
        background: $primary-color;
        transform: rotate(45deg);
        left: 0;
        top: 13px;
    }
}
.subtitle {
    display: flex;
    color: $primary-color;
    position: relative;
    font-size: $font-md;
    @include PC-sm {
        font-size: $font-smd;
    }
    img {
        margin-right: 4px;
    }
}
.sub-subtitle {
    color: $primary-color;
    font-size: $font-smd;
    font-weight: $font-bold;
    padding: 0 12px;
    &:before {
        content: " ";
        display: inline-block;
        margin-right: 7px;
        background: url("../images/ic-title-arrow.svg") no-repeat bottom;
        background-size: contain;
        width: 18px;
        height: 18px;
    }
    @include PC-sm {
        font-size: $font-sm;
    }
}
.icon-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $font-lg;
    color: $primary-color;
    margin-bottom: 0;
    @include PC {
        font-size: $font-md;
    }
    img {
        width: 145px;
        @include PC {
            width: 120px;
        }
    }
}
/*-----------------列表樣式---------------------*/
.accordion {
    &-body {
        position: relative;
        @include Pad {
            font-size: $font-xs;
        }
        &:before {
            content: "A.";
            display: block;
            position: absolute;
            left: 20px;
            top: 0;
            color: $primary-color;
            font-size: $font-lg;
            @include Pad {
                display: none;
            }
        }
    }
    &-item {
        border: none;
    }
    &-button {
        background: $gray300;
        color: $gray500;
        margin-bottom: 16px;
        border-radius: 8px !important;
        padding: 12px 45px 12px 18px;
        position: relative;
        line-height: 1.3;
        @include Pad {
            padding: 12px 40px 12px 12px;
            font-size: $font-xs;
            margin-bottom: 12px;
        }
        &:after {
            background: url(../images/ic-arrow-open.svg);
            background-size: 100%;
            position: absolute;
            right: 18px;
            top: calc(50% - 10px);
            @include Pad {
                right: 12px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $primary-color;
            color: $white;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-arrow-close.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
    &-id {
        font-size: $font-lg;
        margin-right: 12px;
        @include Pad {
            margin-right: 8px;
            font-size: $font-md;
        }
        span {
            font-size: $font-sm;
            margin-left: 3px;
        }
    }
    &-pic {
        width: 100%;
        margin: 16px auto;
    }
    &-pic-inline {
        display: inline;
    }
    &-list {
        li {
            margin-bottom: 10px;
        }
    }
    &-btn {
        display: inline-flex;
        img {
            width: 20px;
            @include Pad {
                width: 16px;
            }
        }
    }
}
.accordion-stable {
    .accordion-button:not(.collapsed) {
        background: $stable-xlight-color;
    }
    .accordion-body:before {
        color: $stable-xlight-color;
    }
}
.circle-card {
    position: relative;
    background: $white;
    border-radius: $w-xxs;
    border: 2px solid $primary-xxlight-color;
    padding: 110px $w-xl $w-lg $w-xl;
    margin-top: 95px;
    height: calc(100% - 95px);
    font-size: $font-smd;
    @include PC {
        font-size: $font-sm;
        padding: 90px $w-lg $w-md $w-lg;
        margin-top: 80px;
        height: calc(100% - 80px);
    }
    @include PC-sm {
        padding: 70px $w-md $w-md $w-md;
        margin-top: 70px;
        height: calc(100% - 70px);
    }
    @include Pad {
        font-size: $font-xs;
        margin-top: 50px;
        height: calc(100% - 50px);
    }
    &-img {
        border-radius: 50%;
        background: $primary-color;
        width: 190px;
        height: 190px;
        position: absolute;
        left: calc(50% - 95px);
        top: -95px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include PC {
            width: 150px;
            height: 150px;
            left: calc(50% - 75px);
            top: -75px;
        }
        @include PC-sm {
            width: 120px;
            height: 120px;
            left: calc(50% - 60px);
            top: -60px;
        }
        @include Pad {
            width: 100px;
            height: 100px;
            left: calc(50% - 50px);
            top: -50px;
        }
        img {
            width: 60%;
            max-width: 100px;
        }
    }
    &-title {
        font-size: $font-lg;
        font-weight: $font-heavy;
        @include PC {
            font-size: $font-md;
        }
        @include PC-sm {
            font-size: $font-smd;
        }
    }
}
.card-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: $w-lg;
    height: 100%;
    &-title {
        font-weight: $font-heavy;
        display: flex;
        align-items: center;
        margin-bottom: $w-lg;
        @include PC {
            font-size: $font-md;
        }
        @include mobile-lg {
            font-size: $font-smd;
            margin-bottom: $w-sm;
        }
        &::before {
            content: attr(data-order);
            display: inline;
            font-size: $font-xxxl;
            color: $primary-color;
            font-weight: $font-xlight;
            margin-right: $w-xs;
            @include PC {
                font-size: $font-xl;
            }
            @include mobile-lg {
                font-size: $font-lg;
            }
        }
    }
    &-img {
        width: 100px;
        margin-bottom: $w-xl;
        @include mobile-lg {
            width: 80px;
            margin-bottom: $w-sm;
        }
    }
    &-txt {
        font-size: $font-smd;
        @include PC {
            font-size: $font-sm;
        }
        @include mobile-lg {
            font-size: $font-xs;
        }
    }
}
.card-list-stable {
    .card-list-item:nth-child(odd) {
        .card-list-link {
            background: $stable-xlight-color;
            &:hover {
                background: $stable-color;
            }
        }
    }
}
.numberl-circle-list {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    @include mobile-lg {
        align-items: flex-start;
    }
    &-item {
        display: inline-block;
        background: $white;
        border-radius: $w-xl $w-xs $w-xs $w-xl;
        color: $primary-color;
        font-size: $font-md;
        font-weight: $font-bold;
        padding: $w-xxs $w-md $w-xxs 60px;
        position: relative;
        @include PC {
            font-size: $font-smd;
        }
        @include Pad {
            font-size: $font-sm;
            border-radius: $w-xxs;
            padding: $w-xxs $w-sm $w-xxs 55px;
            text-align: left;
        }
        @include mobile-lg {
            width: 100%;
        }

        &:before {
            position: absolute;
            content: attr(data-order);
            display: flex;
            width: 44px;
            height: 44px;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            font-size: $font-lg;
            font-weight: $font-light;
            background: $secondary-color;
            color: $white;
            left: 0;
            top: 0;
            border: 3px solid $white;
            @include PC {
                width: 42px;
                height: 42px;
                font-size: $font-md;
            }
            @include Pad {
                width: 40px;
                border-radius: $w-xxs 0 0 $w-xxs;
                height: 100%;
                font-size: $font-smd;
                border: 2px solid $white;
            }
        }
    }
}
.number-list {
    list-style-type: decimal;
    padding-left: 25px;
    color: lighten($gray900, 10%);
    li {
        margin-bottom: 30px;
    }
    li::marker {
        color: $assist-color;
    }
}
.txt-list {
    li {
        display: flex;
        align-items: start;
        .txt-badge {
            background: $gray500;
            color: $white;
            border-radius: 8px;
            padding: 0 6px;
            margin: 2px 10px 0 0;
            font-size: $font-xs;
        }
    }
}
.txt-btn-list {
    li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include Pad {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .txt-btn-list-txt {
        width: calc(100% - 158px);
        @include Pad {
            margin-bottom: 10px;
            width: 100%;
        }
        &-en {
            width: calc(100% - 295px);
            @include PC {
                margin-bottom: 10px;
                width: 100%;
            }
        }
    }
}
.tag-list {
    li {
        display: flex;
        align-items: flex-start;
        &:last-child {
            border-bottom: none;
        }
        .tag-list-tag {
            background: $primary-light-color;
            color: $gray900;
            padding: 0 8px;
            border-radius: 5px;
            margin-right: 15px;
            font-size: $font-sm;
            text-align: center;
            flex: 0 0 90px;
        }
    }
}
.tag-list-header {
    border-radius: 30px 30px 0 0;
    background: $gray500;
    margin-bottom: 0;
    padding: 10px;
}
.list-group-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-left-width: 5px !important;
    @include mobile-md {
        flex-direction: column;
        align-items: start;
        &.list-group-header {
            display: none;
        }
    }
    &:hover {
        background: $primary-color;
    }
}
.list-badge {
    border-left: 1px solid $gray500;
    padding: 0 0 0 20px;
    color: $primary-color;
    @include Pad {
        padding: 0 0 0 10px;
    }
}
.disabled {
    pointer-events: none !important;
}
/*-----------------區塊樣式---------------------*/
.form-box {
    border-radius: 8px;
    padding: 24px 0;
    position: relative;
    z-index: 5;
    @include PC-sm {
        padding: 16px 0;
    }
    @include Pad {
        margin-top: 50px;
    }
    &-img {
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 270px;
        @include PC {
            width: 240px;
            left: 40px;
        }
        @include PC-sm {
            width: 200px;
            bottom: -16px;
        }
        @include Pad {
            width: 150px;
            bottom: 34px;
            left: calc(50% - 100px);
        }
    }
    &-input {
        height: 42px;
        @include PC-sm {
            height: 35px;
        }
    }
    &-btn {
        height: 42px;
        @include PC-sm {
            font-size: $font-xsm;
            height: 35px;
        }
        @include Pad {
            padding: 0 12px;
            width: 100%;
            img {
                width: 16px;
            }
        }
        @include mobile-md {
            font-size: $font-xs;
            padding: 0 8px;
            width: 100%;
        }
        @include mobile-sm {
            p {
                display: none;
            }
        }
    }
}
.info-box {
    background: $white;
    border-radius: 8px;
    border: 1px solid $primary-color;
    border-left: 6px solid $primary-color;
    border-right: 6px solid $primary-color;
    padding: 20px;
}
.notice-box {
    display: inline-block;
    padding: 2px 15px;
    font-size: $font-xs;
    border-radius: 6px;
    background: $gray300;
}
.top-function {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: $font-sm;
    flex-wrap: wrap;
    @include Pad {
        font-size: $font-xs;
    }
    &-info {
        @include mobile-lg {
            width: 100%;
            flex-shrink: 0;
            margin-bottom: 4px;
        }
    }
    &-list {
        display: flex;
        padding-left: $w-sm;
        margin-left: $w-sm;
        border-left: 1px solid $gray500;
        @include PC-sm {
            border-left: 0;
            margin-left: 0;
        }
        @include mobile-lg {
            padding-left: 0;
            margin-right: auto;
        }
        &-item {
            display: inline-flex;
            white-space: nowrap;
            margin-right: $w-sm;
            flex-shrink: 0;
            &:last-child {
                margin-right: 0;
            }
            img {
                margin-right: 4px;
            }
        }
    }
    button {
        font-size: $font-sm;
        @include Pad {
            font-size: $font-xs;
        }
    }
}
/*---------------------表格樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .table-bd-#{$key} {
        th,
        td {
            border-color: $value;
        }
    }
}
.modal {
    .table-wrapper {
        padding: 0;
        height: 530px;
        thead {
            position: sticky;
            top: 0;
            z-index: 1;
        }
        td {
            height: 50px;
        }
    }
}
.table {
    color: $gray900;
    font-size: $font-xs;
    th {
        background: $primary-light-color;
        color: $tertiary-color;
        font-weight: $font-light;
        text-align: center;
        position: sticky;
    }
    td {
        text-align: center;
        vertical-align: middle;
    }
    & > :not(:first-child) {
        border-top: none;
    }
    .btn {
        opacity: 0.8;
        &:hover {
            opacity: 1;
        }
    }
}
.table-rwd {
    min-width: 100%;
    tr {
        cursor: pointer;
        @include Pad {
            display: flex;
            flex-wrap: wrap;
        }
        &:hover {
            td {
                background: $primary-light-color;
            }
        }
    }
    th {
        display: table-cell;
        background: $primary-color;
        color: $white;
        font-weight: $font-light;
        font-size: $font-sm;
        text-align: center;
        padding: 6px 0;
        position: sticky;
        top: 0;
        z-index: 10;
        @include Pad {
            display: none;
        }
    }
    td {
        display: table-cell;
        font-size: $font-smd;
        padding: $w-xs $w-md;
        color: $gray800 !important;
        vertical-align: middle;
        @include PC-sm{
            padding: $w-xxs $w-sm;
        }
        @include Pad {
            font-size: $font-sm;
            display: block;
            text-align: left !important;
            &:nth-child(1) {
                color: $primary-color !important;
                width: 50%;
                padding-top: $w-xxs;
                padding-bottom: 0;
            }
            &:nth-child(2) {
                color: $primary-color !important;
                width: 50%;
                text-align: right !important;
                padding-top: $w-xxs;
                padding-bottom: 0;
            }
            &:nth-child(3) {
                width: 100%;
            }
        }
    }
}
.table-rwd-notice{
    display: inline-flex;
    align-items: start;
    position: relative;
    &:before{
        content: "";
        position: absolute;
        left: -16px;
        width: 1px;
        height: 100%;
        background: $gray500;
        @include Pad{
            display: none;
        }
    }
}
.table-rwd-label {
    td {
        @include Pad {
            display: flex;
            width: 100% !important;
            &:nth-child(1){
                padding-bottom: 0 !important;
                &:before{
                    display: none;
                }
            }
            &:nth-child(2){
                color: $gray700;
                padding-bottom: 8px !important;
            }
            &:nth-child(2) {
                text-align: left !important;
                color: $gray800 !important;
            }
            &:last-child{
                padding-bottom: 16px !important;
            }
        }
    }
    td:before {
        display: none;
        color: $gray900;
        flex-shrink: 0;
        content: attr(data-th) "：";
        @include Pad {
            display: inline-block;
        }
    }
}
.table-striped > tbody > tr:nth-of-type(odd) {
    background: $gray100;
}
.table-link {
    display: block;
    width: calc(100% + 1.5em);
    height: calc(100% + 1.5em);
    margin: -0.75em;
    padding: 0.75em;
    color: $gray900;
    border: 1px solid transparent;
    position: relative;
    &:after {
        position: absolute;
        content: "";
        width: 18px;
        height: 18px;
        background: url(../images/icon-plus.svg) no-repeat center;
        background-size: contain;
        right: 5px;
        bottom: 5px;
        opacity: 0.4;
    }
    &:hover {
        border: 1px solid $primary-color;
        background: $white;
        color: $gray900;
    }
}
.table-height {
    //讓td內容器可以高100%
    td {
        height: 1px;
    }
}
.table-item {
    display: inline-block;
    background: $white;
    border-radius: 4px;
    padding: 3px 2px 3px 10px;
    border: 1px solid $primary-color;
    .btn {
        padding: 5px;
        font-size: $font-xs;
        color: $danger-color;
        opacity: 0.8;
        &:hover {
            opacity: 1;
        }
        img {
            width: 10px;
        }
    }
}
.table-form {
    .table-label {
        background: $gray500;
        color: $white !important;
    }
    .table-sub-label {
        font-size: $font-xxs;
        display: block;
        color: darken($color: $pale-color, $amount: 10%);
    }
    .form-control,
    .form-select {
        font-size: $font-sm;
    }
    td {
        vertical-align: middle;
    }
}
/*---------------------捲軸樣式---------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray100;
        border-radius: 10px;
        transition: all 0.2s ease;
        opacity: 0;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        border-radius: 10px;
        background-color: $gray400;
        transition: all 0.2s ease;
        opacity: 0;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray400;
        transition: all 0.2s ease;
        opacity: 0;
    }
}
.scrollbar-primary {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        border-radius: 10px;
        transition: all 0.2s ease;
        opacity: 0;
        background: $gray200;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        border-radius: 10px;
        background-color: $primary-color;
        transition: all 0.2s ease;
        opacity: 0;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        transition: all 0.2s ease;
        background-color: $primary-color;
        opacity: 0;
    }
}
.scrollx-none {
    overflow-x: hidden;
}
.scrolly-none {
    overflow-y: hidden;
}
/*---------------------標題樣式---------------------*/
.item_text {
    display: inline-block;
    background: $primary-color;
    color: lighten($color: $primary-color, $amount: 15%);
    padding: 0px 10px;
    margin: 5px 10px 5px 0;
    border-radius: 5px;
}

.info-line {
    //線條小標
    display: flex;
    justify-content: space-between;
    position: relative;
    &::after {
        content: "";
        display: block;
        position: absolute;
        background: $gray900;
        width: 100%;
        height: 1px;
        left: 0;
        top: 12px;
        z-index: 50;
    }
    span {
        background: $white;
        position: relative;
        z-index: 100;
        padding: 0 5px;
    }
}
.article-title {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $gray900;
    font-weight: $font-bold;
    padding-bottom: 15px;
    flex-wrap: wrap;
    @include PC {
        flex-direction: column-reverse;
        font-size: $font-xl;
    }
    @include PC {
        font-size: $font-lg;
        font-weight: $font-heavy;
        padding-bottom: 15px;
    }
}
.article-text {
    line-height: 1.8;
    margin-bottom: 35px;
}

.line-higher {
    line-height: 1.8;
    @include mobile-lg {
        line-height: 1.5;
    }
}
.map-title {
    background: lighten($color: $primary-color, $amount: 25%);
    color: $primary-color;
    @include PC {
        font-size: $font-md;
    }
}
.media-title {
    @include mobile-lg {
        font-size: $font-xs;
    }
}
.basic-title {
    font-size: $font-sm;
    font-weight: $font-heavy;
}
/*-----------------列表樣式---------------------*/
.img-list {
    &-box {
        border-radius: 10px;
        border: 4px solid $primary-light-color;
        overflow: hidden;
        height: 90px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
            border: 4px solid $primary-light-color;
        }
        img {
            height: 100%;
