    &-add {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        border: 2px dashed $primary-light-color;
        color: $primary-light-color;
        font-size: $font-xs;
        height: 90px;
        &:hover {
            color: $primary-color;
            border: 2px dashed $primary-color;
