    .mark-menu-item {
        border-right: 1px solid lighten($color: $primary-color, $amount: 10%);
        &:first-child {
            a {
                border-radius: 8px 0 0 0;
            }
        }
        &:last-child {
            border-right: none;
            a {
                border-radius: 0 5px 0 0;
            }
        }
    }
    a {
        display: block;
        width: 100%;
        background: lighten($color: $primary-color, $amount: 15%);
        color: $gray900;
        position: relative;
        padding: 13px 25px;
        font-size: $font-sm;
        &.active {
            background: $white;
            font-weight: $font-heavy;
