/*--------------------內頁開始------------------*/
.carousel-indicators [data-bs-target] {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 1px solid $primary-color;
    background: $white;
    margin: 0 $w-xxs $w-sm $w-xxs;
    opacity: 1;
    &.active {
        background: $primary-color;
    }
}
.carousel-control-prev,
.carousel-control-next {
    width: 60px;
    height: 60px;
    background: $primary-color;
    border-radius: 50%;
    opacity: 1;
    top: calc(50% - 30px);
    @include PC-md {
        width: 50px;
        height: 50px;
        top: calc(50% - 25px);
    }
    @include PC-sm {
        width: 40px;
        height: 40px;
        top: calc(50% - 20px);
    }
    img {
        width: 50%;
    }
}
.carousel-control-prev {
    left: 40px;
    @include PC {
        left: 20px;
    }
    img {
        transform: scaleX(-1);
    }
}
.carousel-control-next {
    right: 40px;
    @include PC {
        right: 20px;
    }
}
.page-slide {
    position: relative;
    z-index: 1;
    &-item {
        overflow: hidden;
        justify-content: center;
        align-items: flex-end;
        @include PC-sm {
            height: 500px;
        }
        @include Pad {
            height: 690px;
            align-items: end;
        }
        @include mobile-lg {
            height: 650px;
        }
        @include mobile-md {
            height: 600px;
        }
        &.active {
            display: flex;
        }
        & > img {
            @include PC-lg {
                margin-left: 18%;
            }
            @include PC {
                width: 1200px;
                max-width: 1200px;
                margin-left: 30%;
            }
            @include PC-sm {
                width: 1000px;
            }
            @include Pad {
                width: 768px;
                margin-left: 0;
            }
            @include mobile-lg {
                width: 650px;
            }
            @include mobile-md {
                width: 600px;
            }
        }
    }
    &-item[data-con="plan"] {
        @include PC-sm{
            align-items: start;
            height: auto;
        }
        img {
            @include PC-lg {
                width: 100%;
                max-width: 100%;
                margin-left: 0;
            }
            @include PC-sm {
                margin-top: 20px;
            }
            @include Pad{
                margin: 0 auto;
                margin-bottom: 50px;
            }
            @include mobile-lg {
                max-width: 390px;
                margin-bottom: 20px;
            }
        }
        .page-slide-caption{
            position: static;
            display: flex;
            width: 100%;
            justify-content: flex-start;
            margin-top: -10px;
            margin-bottom: 5%;
            @include Pad{
                margin-top: 50px;
                margin-bottom: 0;
            }
        }
        .page-slide-caption-title{
            font-size: $w-xl;
            transform: scale(1);
            @include PC{
                font-size: $w-lg;
            }
            @include Pad{
                margin-left: 0;
                text-align: left;
            }
        }
    }
    &-en {
        font-size: 4.5rem;
        font-weight: $font-bold;
    }
    &-highlight {
        background: $danger-dark-color;
        color: $white;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        vertical-align: middle;
        width: 100px;
        height: 100px;
        margin-left: $w-xs;
        margin-top: -18px;
        border-radius: 50%;
        text-shadow: none;
    }
    &-caption {
        color: $gray900;
        top: 27%;
        bottom: auto;
        text-align: left;
        @include PC-lg {
            top: 25%;
            left: 12%;
            right: 12%;
        }
        @include PC-md {
            top: 18%;
            left: 5%;
            right: 5%;
        }
        @include PC {
            left: -50px;
            right: 0;
        }
        @include PC-sm {
            top: 18%;
            left: -100px;
        }
        @include Pad {
            left: 0;
            right: 0;
            top: 15px;
            text-align: center;
        }
        &[data-style="normal"] {
            .page-slide-caption-title {
                line-height: 1.2;
                margin-bottom: $w-sm;
            }
            .page-slide-caption-txt {
                line-height: 1.5;
            }
        }

        &-title {
            font-size: 4rem;
            font-weight: $font-heavy;
            line-height: 1.35;
            margin-bottom: 0;
            text-shadow: 0 0 10px $white;
            @include PC-md {
                transform: scale(0.8);
            }
            @include PC {
                transform: scale(0.7);
            }
            @include PC-sm {
                transform: scale(0.6);
            }
            @include Pad {
                width: 130%;
                margin-left: -15%;
            }
            @include mobile-lg {
                width: 160%;
                margin-left: -30%;
            }
            @include mobile-md {
                transform: scale(0.5);
            }
            @include mobile-sm {
                transform: scale(0.45);
                width: 200%;
                margin-left: -50%;
            }
        }
        &-txt {
            font-size: $font-md;
            font-weight: $font-heavy;
            letter-spacing: 1px;
            line-height: 1.1;
            @include PC-md {
                transform: scale(0.8);
                margin-top: -20px;
            }
            @include PC {
                transform: scale(0.75);
                margin-top: -30px;
                margin-left: 38px;
            }
            @include PC-sm {
                margin-top: -40px;
                margin-left: 9%;
            }
            @include Pad {
                width: 150%;
                margin-left: -25%;
            }
            @include mobile-lg {
                transform: scale(0.7);
                margin-top: -40px;
            }
            @include mobile-md {
                width: 140%;
                margin-left: -20%;
                margin-top: -50px;
            }
            @include mobile-sm {
                width: 140%;
                margin-top: -55px;
                margin-left: -20%;
                br {
                    display: none;
                }
            }
            &-lg {
                font-size: $font-xl;
                font-weight: $font-xheavy;
            }
        }
    }
    &-action {
        display: flex;
        @include PC-md {
            margin-left: 10%;
            margin-top: -10px;
        }
        @include PC {
            margin-left: 15%;
        }
        @include PC-sm {
            margin-left: 20%;
            margin-top: -15px;
        }
        @include Pad {
            justify-content: center;
            margin-left: 0;
        }
        @include mobile-md {
            margin-top: -15px;
        }
        .btn {
            width: 220px;
            height: 60px;
            line-height: 60px;
            padding: $w-sm 0;
            font-size: $font-md;
            font-weight: $font-bold;
            @include PC-md {
                width: 170px;
                height: 55px;
                line-height: 55px;
                font-size: $font-smd;
            }
            @include PC-sm {
                height: 50px;
                line-height: 50px;
                font-size: $font-sm;
                width: 140px;
            }
            @include mobile-lg {
                width: 125px;
            }
        }
    }
}
