/*--溢出文字點點點--*/
@mixin over_text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}
/* --------------按鈕設定開始--------------- */
@mixin btn-dark($bgcolor) {
    color: $white !important;
    background: $bgcolor!important;
    border: 1px solid $bgcolor;
    padding: 4px 16px;
    border-radius: $w-xxs;
    &:hover {
        color: $white!important;
        background: darken($bgcolor, 10%)!important;
    }
    img{
        margin-right: 4px;
    }
}
@mixin btn-border($color) {
    color: $color !important;
    background: $white!important;
    border: 2px solid $gray300;
    border-radius: $w-xxs;
    position: relative;
    padding: 3px $w-sm;
    img{
        margin-right: $w-xxxs;
    }
    &:hover {
        border: 2px solid $color;
        color: $white;
    }
}
