body {
	font-family: Noto Sans TC, 微軟正黑體;
}
.container {
	max-width: 1045px;
}
header {
	height: 82px;
	@include PC-sm {
		height: 70px;
	}
	@include Pad {
		height: 63px;
	}
}
/*--------------------主選單開始------------------*/
.navbar {
	background: $white;
	border-bottom: 3px solid $primary-color;
	align-items: center;
	&-brand {
		@include PC-sm {
			width: 120px;
		}
		@include Pad {
			width: 100px;
		}
		@include mobile-md {
			width: 80px;
			margin-right: $w-xs;
		}
	}
	&-title {
		font-size: $font-lg;
		font-weight: $font-light;
		color: $gray900;
		@include Pad {
			font-size: $font-sm !important;
		}
		@include mobile-md {
			font-size: $font-xs !important;
		}
	}
	&-collapse {
		position: absolute;
		width: 100%;
		left: 0;
		top: calc(100% + 8px);
		@include PC-sm {
			flex-shrink: 1;
		}
		@include Pad {
			background: $primary-color;
			position: absolute;
			left: 0;
			width: 100%;
		}
	}
	&-function {
		display: flex;
		padding: 0 $w-xs;
		flex-direction: column;
		border-right: 1px solid $gray400;
		flex-shrink: 0;
		@include PC {
			display: none;
		}
		&:first-child {
			margin-left: auto;
		}
		&-title {
			display: flex;
			justify-content: center;
			font-size: $font-xsm;
			font-weight: $font-light;
			img {
				margin-right: $w-xxxs;
			}
		}
		&-list {
			display: flex;
		}
		&-item {
			&:last-child {
				.navbar-function-link {
					padding-right: 0;
				}
			}
		}
		&-link {
			position: relative;
			color: $gray900;
			padding: 0 $w-xs 0 $w-xs;
			&:hover {
				color: $primary-color;
			}
			&::before {
				content: "";
				display: block;
				position: absolute;
				width: 4px;
				height: 13px;
				left: 4px;
				top: 6px;
				background: $primary-color;
			}
		}
	}
	&-nav {
		display: flex;
		flex-direction: row;
		flex-shrink: 0;
		padding: 0 0 0 $w-xxs;
		margin-left: auto;
	}
	&-mobile {
		/*往上捲動選單*/
		display: none;
		margin-left: auto;
		&-item {
			&:last-child {
				.navbar-mobile-link {
					margin-right: 0;
				}
			}
		}
		&-link {
			color: $primary-color;
			padding: 0 $w-xxs;
			&:hover {
				color: $gray700;
			}
		}
	}
	&-dropdown {
		/*下拉選單*/
		display: flex;
		justify-content: space-between;
		text-align: center;
		background: $white;
		border: 1px solid $gray500;
		@include Pad{
			height: calc(100% - 63px);
			overflow: auto;
		}
		&-item {
			@include Pad {
				position: relative;
				margin-bottom: $w-sm;
				padding-bottom: $w-xxxs;
				&::after{
					content: "";
					position: absolute;
					left: 4px;
					bottom: 0;
					display: block;
					width: calc(100% - 8px);
					height: 1px;
					background: $primary-color;
				}
				&:nth-last-child(1),&:nth-last-child(2){
					&::after{
						display: none;
					}
				}
			}
		}
		&-title {
			font-size: $font-smd;
			color: $gray900;
			border-bottom: 1px solid $primary-color;
			padding-bottom: $w-xxs;
			@include PC-sm {
				font-size: $font-sm;
			}
			@include Pad {
				font-size: $font-xsm;
				border-bottom: 0;
				padding-bottom: 0;
			}
		}
		&-link {
			display: block;
			color: $gray700;
			margin-bottom: $w-xxs;
			&:hover {
				color: $gray700;
				opacity: 0.8;
			}
			@include PC-sm {
				font-size: $font-xsm;
			}
			@include Pad {
				font-size: $font-xs;
			}
		}
	}
}
/*-----------------右側選單開始------------------*/
.nav {
	&-item {
		flex-shrink: 0;
	}
	&-link {
		display: flex;
		flex-direction: column;
		align-items: center;
		color: $primary-color !important;
		padding: 0 $w-xxs !important;
		font-size: $font-xsm;
		@include PC-sm {
			color: $white;
			text-align: center;
			font-weight: $font-light;
			img {
				width: 30px;
			}
			p {
				font-size: $font-xs;
			}
		}
		@include Pad {
			img {
				width: 25px;
			}
			p {
				font-size: $font-xxs;
			}
		}
		&:hover {
			opacity: 0.8;
			@include Pad {
				color: $white;
			}
		}
	}
	&-toggler {
		position: relative;
		@include mobile-lg {
			padding: 0 !important;
		}
		&:after {
			content: "";
			position: absolute;
			width: calc(100% + 8px);
			height: 40px;
			left: -4px;
			bottom: -16px;
			background: $primary-color;
			border-radius: $w-xxxs $w-xxxs 0 0;
		}
		&.collapsed {
			&:after {
				display: none;
			}
			p {
				color: $primary-color;
			}
			.nav-toggler-bar {
				transform: rotate(0deg);
				width: 22px;
				@include Pad {
					width: 20px;
				}
			}
			.nav-toggler-bar3 {
				top: 8px;
				@include PC-sm {
					top: 4px;
				}
				@include Pad {
					top: 2px;
				}
			}
			.nav-toggler-bar2 {
				top: 15px;
				opacity: 1;
				@include PC-sm {
					top: 11px;
				}
				@include Pad {
					top: 9px;
				}
			}
			.nav-toggler-bar1 {
				top: 23px;
				@include PC-sm {
					top: 19px;
				}
				@include Pad {
					top: 15px;
				}
			}
		}
		&-img {
			width: 34px;
			height: 34px;
			position: relative;
			@include PC-sm {
				width: 30px;
				height: 30px;
			}
			@include Pad {
				width: 25px;
				height: 25px;
			}
		}
		p {
			color: $white;
			position: relative;
			z-index: 3;
		}
		&-bar {
			top: 13px;
			left: 4px;
			width: 24px;
			height: 2.5px;
			position: absolute;
			background: $primary-color;
			@include PC-sm {
				top: 8px;
				width: 20px;
			}
			@include mobile-md {
				top: 8px;
				width: 18px;
				height: 2px;
			}
		}
		&-bar1 {
			transform: rotate(-45deg);
		}
		&-bar2 {
			opacity: 0;
		}
		&-bar3 {
			transform: rotate(45deg);
		}
	}
}
/*-----------------主選單收合開始------------------*/
.navbar.up {
	padding-top: $w-xxs;
	padding-bottom: $w-xxs;
	@include Pad {
		padding-top: $w-xxxs;
		padding-bottom: $w-xxxs;
	}
	.navbar-brand {
		width: 100px;
		@include Pad {
			width: 80px;
		}
		@include mobile-md {
			width: 70px;
			margin-right: $w-xs;
		}
	}
	.navbar-title {
		font-size: $font-md;
	}
	.navbar-mobile {
		display: flex;
		@include PC-sm {
			display: none;
		}
	}
	.navbar-collapse {
		@include Pad {
			top: calc(100% + 6px);
		}
		@include mobile-xmd {
			top: 100%;
		}
	}
	.navbar-function {
		display: none;
	}
	.navbar-nav {
		img {
			display: none;
		}
	}
	.navbar-nav[data-con="mis"] {
		display: none;
		@include PC-sm {
			display: flex;
			img {
				display: block;
			}
			p {
				display: none;
			}
		}
	}
	.nav-toggler {
		&:after {
			display: none;
		}
		@include mobile-md {
			margin-top: 2px;
		}
	}
	.nav-toggler.collapsed {
		.nav-toggler-bar3 {
			@include PC-sm {
				top: 4px;
			}
		}
		.nav-toggler-bar2 {
			@include PC-sm {
				top: 11px;
			}
		}
		.nav-toggler-bar1 {
			@include PC-sm {
				top: 18px;
			}
		}
	}
	.nav-toggler-bar {
		@include PC-sm {
			top: 12px;
			width: 22px;
		}
		@include mobile-md {
			top: 8px;
			width: 18px;
			height: 2px;
		}
	}

	.nav-link {
		font-weight: $font-light;
		img {
			display: none;
		}
		&:hover {
			color: $secondary-light-color;
		}
	}
	.btn {
		background: $white !important;
		color: $primary-color !important;
		border: 1px solid $white;
		&:hover {
			background: $alert-color !important;
			color: $white !important;
		}
	}
}

/*--------------------footer開始------------------*/
footer{
	margin-top: 80px;
}
.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	color: $white;
	font-size: $font-xsm;
	font-weight: $font-light;
	@include PC {
		flex-direction: column;
	}
	@include PC-md {
		font-size: $font-xs;
	}
	@include Pad {
		font-size: $font-xxs;
		flex-direction: column;
	}
	&-logo {
		width: 210px;
		@include PC {
			width: 180px;
		}
		@include Pad {
			width: 150px;
		}
	}
	&-nav {
		display: flex;
	}
	&-item {
		&:last-child {
			.footer-link {
				border-right: 0;
			}
		}
	}
	&-link {
		color: $white !important;
		padding: 0 $w-sm;
		border-right: 1px solid $white;
		&:hover {
			opacity: 0.8;
		}
	}
	&-bottom {
		width: 100%;
		flex-shrink: 0;
		display: flex;
		justify-content: space-between;
		text-align: center;
		font-size: $font-xs;
		@include Pad {
			font-size: $font-xxs;
		}
		@include PC-sm {
			align-items: center;
			flex-direction: column;
		}
		p:last-child {
			font-size: $font-xxxs;
		}
	}
}
.gotop {
	position: fixed;
	right: calc(50% - 512px);
	bottom: 170px;
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: $white;
	border-radius: 10px;
	opacity: 0;
	border: 1px solid $primary-color;
	@include PC-sm {
		width: 44px;
		height: 44px;
		line-height: 44px;
		font-size: $font-xxs;
		bottom: 68px;
	}

	&:hover {
		color: $white;
		background: $alert-light-color;
		img {
			filter: invert(100%) sepia(0%) saturate(7469%) hue-rotate(68deg) brightness(500%) contrast(97%);
		}
	}

	&.show {
		opacity: 1;
	}
}
/*--------------------視窗開始------------------*/
.modal {
	&-content {
		background: $primary-color;
		border-radius: $w-md;
		overflow: hidden;
	}
	&-header {
		border: 0;
		color: $white;
		display: flex;
		justify-content: center;
		position: relative;
		button {
			position: absolute;
			right: 10px;
			&:focus,
			&:active {
				border: none !important;
			}
		}
	}
	&-body {
		background: $white;
		font-size: $font-smd;
		min-height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&-footer {
		background: $white;
		border: 0;
		display: flex;
		justify-content: center;
	}
	&-action {
		border-radius: $w-xxxs;
		letter-spacing: 2px;
		max-width: 200px;
	}
}
.section {
	padding-top: 64px;
	font-size: $font-smd;
	@include PC-lg{
		padding-top: 32px;
	}
	@include Pad{
			font-size: $font-sm;
		}
	&-container {
		background: $white;
		border: 1px solid $gray200;
		padding: $w-md $w-xl;
		position: relative;
		@include Pad{
			padding: $w-md $w-lg;
		}
		&-id{
			position: absolute;
			width: 30px;
			height: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $gray300;
			color: $white;
			border-radius: 0 $w-xxxs $w-xxxs 0;
			left: 0;
			top: $w-md;
			@include Pad{
				width: 25px;
				height: 25px;
			}
		}
	}
}
.form-control,
.form-select {
	border: none;
	border-bottom: 1px solid $gray700;
	border-radius: 0;
	font-size: $font-smd;
		@include Pad{
			font-size: $font-sm;
		}
}
.form-control[data-con="upload"]{
	border-radius: $w-xxxs;
	background: $gray100;
	border: none;
}
textarea.form-control{
	border: 1px solid $primary-color;
	border-radius: $w-xxxs;
}
.form-check-input[type=checkbox] {
    border-radius: 0.25em;
}
.col-form-label{
	font-weight: $font-heavy;
}
.form-check-label{
	font-size: $font-sm;
}
.form-select {
	background: url("../images/ic-dropdown.svg") no-repeat right center;
	font-size: $font-smd;
		@include Pad{
			font-size: $font-sm;
		}
}
.form-check-input:checked[type=checkbox] {
    background: url("../images/ic-check.svg") no-repeat center $primary-color;
	background-size: 10px;
	border: 1px solid $primary-color;
}
.form-check-input[type=radio] {
	border-radius: 50%;
	background-size: 10px;
	border: 1px solid $gray500;
}
.form-check-input:checked[type=radio] {
	display: flex;
	justify-content: center;
	align-items: center;
	background: $white;
	&::before{
		content: "";
		position: absolute;
		background: $primary-color;
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
}
.input-group-text{
	background: none;
	height: 100%;
	border-radius: 0;
	border: 0;
	border-bottom: 1px solid $gray700;
}
.form-dropdown {
	position: relative;
	button {
		width: 100%;
		border: none;
		border-bottom: 1px solid $gray800 !important;
		border-radius: 0 !important;
		background: url("../images/ic-dropdown.svg") no-repeat right center $white !important;
		color: $gray800 !important;
		padding-right: $w-md;
		text-align: left;
		font-size: $font-smd;
		@include Pad{
			font-size: $font-sm;
		}
		&.active{
		color: $primary-color !important;
	}
	}
	&-content {
		position: absolute;
		left: 0;
		top: 100%;
		min-width: 350px;
		z-index: 10;
		@include PC-sm{
			left: auto;
			right: 0;
		}
	}
}
.no-file{
	background: $gray100;
	border: 1px dashed $gray500;
	color: $gray600;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 80px;
	@include Pad{
			font-size: $font-sm;
		}
}
.file-list{
	background: $gray100;
	border-radius: $w-xxxs;
	padding: $w-sm;
	&-item{
		display: flex;
		align-items: center;
		border-bottom: 3px solid $white;
		padding: 4px 0;
		@include Pad{
			flex-wrap: wrap;
			justify-content: center;
		}
		&:last-child{
			border: none;
		}
	}
	&-info{
		border-right: 1px solid $gray500;
		font-size: $font-sm;
		width: 50px;
		justify-content: flex-start;
		@include Pad{
			width: 100%;
			border: 0;
		}
	}
	&-txt{
		padding-left: $w-sm;
		@include Pad{
			width: 100%;
			flex-shrink: 0;
			padding-left: 0;
			font-size: $font-sm;
		}
	}
}
